
body,html{
    height: 100%;
    width: 100%;
    background-color: grey;
    margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height: 100%;
  max-width: 100%;
}

#home {
  height: 100%;
  width: 100%;
}



#page-body {
  overflow: hidden;
  position: relative;
  display: block;
  margin: 50px;
  border: 2px solid greenyellow;
  height: 90%;
  width: 90%;
  margin-left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
#input-holder {
  overflow: auto;
  width: 30%;
  height: 100%;
  max-height: 100%;
  float: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #3bb4f1;
  background-image: url(/static/img/bg.56d462d.png);
  background-repeat: no-repeat;
}
#img-holder {
  float: right;
  height: 100%;
  width: 70%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: white;
}
#img-box {
  width: 100%;
  max-height: 80%;
  overflow: auto;
}
#foot {
  position: absolute;
  bottom: -1rem;
  left: 4rem;
  height: 1rem;
}

