  height: 64px;
  width: 64px;
  /* width: 32px;
  height: 32px; */
}
.large-img {
  width: 30rem;
  height: 30rem;
  max-height: 100%;
  max-width: 100%;
  /* height: auto; */
  margin: 0;
}
#large-img-holder {
  max-width: 90%;
  max-height: 60%;
  width: 320px;
  height: 320px;
  margin: 0 auto;
}
#small-img-holder {
  /* flex-wrap: wrap; */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 15px;
  margin: 2rem 0 auto;
  height: 70px;
  width: 576px;
  max-width: 90%;
  overflow-x: auto;
  overflow-y: hidden;
  border-left: 15px solid rgb(220, 220, 220);
  border-right: 20px solid rgb(220, 220, 220);
  background: rgb(220 , 220, 220);
  margin-left: 50%;
  -webkit-transform: translate(-50%,0);
          transform: translate(-50%,0);
  /* border: 1.5px solid black; */
}
